@import url(https://fonts.googleapis.com/css?family=Lato&display=swap);
@import url(https://fonts.googleapis.com/css?family=Open+Sans&display=swap);
@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
body {
  margin: 0;
  font-family: 'Lato', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{width:100%;min-height:100vh;display:grid;grid-template-areas:"header header" "main main" "footer footer";grid-template-rows:auto 1fr auto;background-color:#f0efef}.App .Header{grid-area:header}.App .App-main-content{grid-area:main}.App>.Footer{grid-area:footer}

.Footer{background-color:white;text-align:center}

.Header{display:flex;justify-content:center;align-items:center;padding:10px 15vw;background-color:black}.Header h1{color:white;margin:0;margin-right:30px;font-weight:normal}.Header h1 strong{font-weight:bold}.Header .Header-burger-toggle{background-color:black;padding:0 0.37em;border:0.2em solid white}.Header .Header-burger-toggle span{display:block;width:2.0em;height:0.2em;margin:0.5em 0;background-color:white}.Header .Header-burger-toggle.toggled{border:0.2em solid red}.Header .Header-burger-toggle.toggled span{background-color:red}.Header .Header-burger-toggle.toggled+.Header-menu{display:block}.Header .Header-menu{display:none;position:absolute;top:60px;left:0;right:0;z-index:100;background-color:black}.Header .Header-menu .Header-links a{display:block;margin:0 0px;padding:10px 20px;min-width:70px;text-align:center;font-size:1.2em;text-decoration:none;border:2px solid black;color:#999;-webkit-transition:0.1s ease-out all;transition:0.1s ease-out all}.Header .Header-menu .Header-links a:hover{border:2px solid #333}.Header .Header-menu .Header-links a.active{border:2px solid white;color:white}@media screen and (min-width: 900px){.Header{justify-content:flex-start}.Header .Header-burger-toggle{display:none}.Header .Header-menu{display:block;position:static}.Header .Header-menu .Header-links{display:flex}.Header .Header-menu .Header-links a{margin:0 5px}}

.QuizOptionComponent{margin:10px 0;padding:5px 20px;background-color:#ffff}.QuizOptionComponent.wrong{background-color:#f12c2c}.QuizOptionComponent.correct{background-color:#73f573}

.QuizQuestionPanel{display:grid;grid-template-areas:"q q" "o o" "s n";grid-template-rows:auto 1fr auto;grid-template-columns:1fr auto;height:100%}.QuizQuestionPanel .QuizQuestionPanel-question{grid-area:q;display:flex;align-items:center;font-size:1.5em}.QuizQuestionPanel .QuizQuestionPanel-options{grid-area:o;flex:1 1}.QuizQuestionPanel .QuizQuestionPanel-next-button{grid-area:n;margin-bottom:30px;padding:10px 40px;background-color:#3874e4;color:white;border:none}.QuizQuestionPanel .QuizQuestionPanel-score{display:flex;justify-content:center;align-items:center;grid-area:s;height:auto;margin-bottom:30px;padding:5px;margin-right:10px;color:black;border:1px solid black;font-size:1em}

.AnalysisQuiz{height:100%;padding:0 15vw}

.TheoremPanel{display:grid;grid-template-areas:"name name" "prereq-h prereq" "thesis-h thesis";grid-template-columns:auto 1fr;grid-template-rows:auto auto auto;max-width:80vw;padding:1vw 4vw;margin:0.2vw 0;box-shadow:-1px 7px 20px 0px rgba(0,0,0,0.1);font-size:2vw;background-color:white;color:black}.TheoremPanel .TheoremPanel-name{grid-area:name;font-size:1.3em;text-align:center;color:black}.TheoremPanel .TheoremPanel-prereqs-header{grid-area:prereq-h}.TheoremPanel .TheoremPanel-prereqs{grid-area:prereq;margin:0;padding-left:30px;padding-bottom:30px}.TheoremPanel .TheoremPanel-thesis-header{grid-area:thesis-h}.TheoremPanel .TheoremPanel-thesis-header,.TheoremPanel .TheoremPanel-prereqs-header{padding-right:1em;color:#aaa}.TheoremPanel .TheoremPanel-thesis{grid-area:thesis;word-wrap:normal;white-space:normal}@media screen and (min-width: 900px){.TheoremPanel{font-size:1em}}

.TheoremList{box-sizing:border-box;height:100%;display:flex;flex-direction:column;align-items:center;padding:100px 0}.TheoremList .TheoremList-list{display:flex;flex-direction:column}

.TheoremFilter{max-width:1200px;padding:10px;background-color:#d4d1d1;font-size:2vw}.TheoremFilter h1{margin:0 0 10px 0;font-size:1em}.TheoremFilter .TheoremFilter-categories{display:grid;grid-template-rows:repeat(3, 1fr);grid-auto-flow:column}.TheoremFilter .TheoremFilter-categories label{width:auto;display:block}@media screen and (min-width: 900px){.TheoremFilter{font-size:1em}}

.AnalysisHome{display:flex;flex-direction:column;height:100%}.AnalysisHome .AnalysisHome-subpage-links{display:flex;flex-direction:column;align-items:center;margin:20px 0}.AnalysisHome .AnalysisHome-subpage-links .SectionLink{margin:10px 0;min-width:300px}

.SectionLink{padding:10px 20px;border:1px solid black;text-align:center;color:black;text-decoration:none}

.IntegralList{padding:50px;overflow-y:scroll}

.HomePage{height:100%;display:flex;flex-direction:column;align-items:center;background-color:#d80971}.HomePage .HomePage-title{display:block;margin-bottom:0em;font-size:10vh;text-align:center;color:white;font-weight:normal}.HomePage .HomePage-title strong{font-weight:bold}.HomePage .HomePage-message{margin:0;margin-bottom:2em;font-size:3vh;color:white;opacity:0.7}.HomePage .HomePage-subpages{display:flex}.HomePage .HomePage-subpages a{display:flex;justify-content:center;align-items:center;width:130px;height:130px;margin:10px;font-size:1.5em;text-decoration:none;color:white;border:1px solid white}

